// ** React Imports
import { useEffect, useState } from 'react'
import { useParams, Link } from 'react-router-dom'

// ** Store & Actions
import { getCompliance } from '../store'
import { useSelector, useDispatch } from 'react-redux'

// ** Reactstrap Imports
import { Row, Col, Alert } from 'reactstrap'

// ** Compliance View Components
import ComplianceTabs from './Tabs'
import UserInfoCard from './UserInfoCard'

// ** Styles
import '@styles/react/apps/app-users.scss'

const ComplianceView = () => {
  // ** Store Vars
  const store = useSelector(state => state.compliance)
  const dispatch = useDispatch()

  // ** Hooks
  const { id } = useParams()

  // ** Get suer on mount
  useEffect(() => {
    dispatch(getCompliance(id))
  }, [dispatch])

  const [active, setActive] = useState('preview')

  const toggleTab = tab => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  return store.selectedCompliance !== null && store.selectedCompliance !== undefined ? (
    <div className='app-Compliance-view'>
      <Row>
        <Col xl='4' lg='5' xs={{ order: 1 }} md={{ order: 0, size: 5 }}>
          <UserInfoCard selectedCompliance={store.selectedCompliance} />
        </Col>
        <Col xl='8' lg='7' xs={{ order: 0 }} md={{ order: 1, size: 7 }}>
          <ComplianceTabs active={active} toggleTab={toggleTab} selectedCompliance={store.selectedCompliance} />
        </Col>
      </Row>
    </div>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>Compliance not found</h4>
      <div className='alert-body'>
        Compliance with id: {id} doesn't exist. Check list of all Compliances: <Link to='/compliance/list'>Compliances List</Link>
      </div>
    </Alert>
  )
}
export default ComplianceView
