// ** Reactstrap Imports
import {
  Card, CardBody, CardHeader, Row, Table
} from 'reactstrap'

// ** Styles
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { activeOrganization } from '@src/helper/sassHelper'
import moment from 'moment'
const activeOrg = activeOrganization()
const InvoiceList = ({ selectedCompliance }) => {

  // ** Store Vars
  const complaince = selectedCompliance.compliance
  const recurringOptions = [
    '',
    'One time',
    'Monthly',
    'Quaterly',
    'Annually'
  ]

  const dateFormat = (value) => {
    const year = moment().format('y')
    if (value.length === 13) {
      return moment(value, 'x').year(year).format("MMM DD, YYYY")
    } else {
      return moment.unix(value).year(year).format("MMM DD, YYYY")
    }

  }

  return (
    <div className='invoice-list-wrapper'>
      <Card>
        <CardHeader>
          Email Preview
        </CardHeader>
        <CardBody>

          <h5>Dear [Contact Person Name], </h5>
          <Row className='px-2 mt-2'>
            <p>Greetings from <strong>{activeOrg.name}</strong></p>
            <p>Filing required for your <strong>[Client Business Name]</strong></p>
            <p>{complaince.name} is required to be filed {recurringOptions[complaince.frequency]} on or before {dateFormat(complaince.duedate)}</p>
          </Row>
          {complaince.forms !== null &&
            <Row>
              <h5>Filing Details</h5>
              <Table>
                <thead>
                  <tr>
                    <td>
                      Form
                    </td>
                    <td>Govt Fees</td>
                    <td>Late Fees</td>
                    <td>Penalty</td>
                  </tr>
                </thead>
                <tbody>
                  {
                    complaince.forms.map((item, key) => {
                      const obj = JSON.parse(item)
                      return (

                        <tr key={key}>
                          <td>{obj.name}</td>
                          <td>{obj.filling_fees}</td>
                          <td>{obj.late_fees_duration}</td>
                          <td>{obj.penalty}</td>
                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table>
            </Row>
          }
          <Row>
           <div dangerouslySetInnerHTML={{ __html: complaince.description.replace(/(\\n)/g, "") }}></div>
          </Row>
        </CardBody>
      </Card>
    </div>
  )
}

export default InvoiceList
