// ** React Imports
import { Fragment } from 'react'

// ** Reactstrap Imports
import { Card, CardBody, Button, Badge } from 'reactstrap'
import { Link, useParams } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'

const frequencyArr = [
  '',
  'One Time',
  'Monthly',
  'Quaterly',
  'Annualy'
]

const reminderOptions = [
  { name: '1 day before', id: 1, value: 1 },
  { name: "2 day's before", id: 2, value: 2 },
  { name: "3 day's before", id: 3, value: 3 },
  { name: "4 day's before", id: 4, value: 4 },
  { name: "5 day's before", id: 5, value: 5 },
  { name: "7 day's before", id: 6, value: 7 },
  { name: "10 day's before", id: 7, value: 10 },
  { name: "15 day's before", id: 8, value: 15 },
  { name: "21 day's before", id: 9, value: 21 },
  { name: "30 day's before", id: 10, value: 30 },
  { name: "2 month's before", id: 11, value: 60 },
  { name: "3 months's before", id: 12, value: 90 },
  { name: "4 months's before", id: 13, value: 120 }
]

const UserInfoCard = ({ selectedCompliance }) => {

  const { id } = useParams()

  const compliance = selectedCompliance.compliance
  // ** render user img
  const renderUserImg = () => {
    return (
      <Avatar
        initials
        color={compliance.avatarColor || 'light-primary'}
        className='rounded mt-3 mb-2'
        content={compliance.name}
        contentStyles={{
          borderRadius: 0,
          fontSize: 'calc(48px)',
          width: '100%',
          height: '100%'
        }}
        style={{
          height: '110px',
          width: '110px'
        }}
      />
    )

  }

  const renderReminder = (val) => {

    const temp = reminderOptions.find((i) => parseInt(val) === parseInt(i.value))
    return temp !== undefined ? temp.name : ''

  }
  return (
    <Fragment>
      <Card>
        <CardBody>
          <div className='user-avatar-section'>
            <div className='d-flex align-items-center flex-column'>
              {renderUserImg()}
              <div className='d-flex flex-column align-items-center text-center'>
                <div className='user-info'>
                  <h4>{selectedCompliance !== null ? compliance.name : 'Eleanor Aguilar'}</h4>
                </div>
              </div>
            </div>
          </div>
          <h4 className='fw-bolder border-bottom pb-50 mb-1'>Details</h4>
          <div className='info-container'>
            {selectedCompliance !== null ? (
              <ul className='list-unstyled'>
                <li className='mb-75'>
                  <span className='fw-bolder me-25'>Name:</span>
                  <span>{compliance.name}</span>
                </li>
                <li className='mb-75'>
                  <span className='fw-bolder me-25'>Frequency:</span>
                  <span>{frequencyArr[compliance.frequency]}</span>
                </li>
                <li className='mb-75'>
                  <span className='fw-bolder me-25'>Reminders:</span>
                  {compliance.reminderonactual.length > 0 &&
                    compliance.reminderonactual.map((obj, k) => {
                      return (
                        <Badge color={'primary'} key={k} className='text-capitalize ms-md-50'>
                          {
                            renderReminder(obj)
                          }
                        </Badge>
                      )
                    })
                  }
                </li>

              </ul>
            ) : null}
          </div>
          <div className='d-flex justify-content-center pt-2'>
            <Button color='primary' tag={Link} to={`/compliance/edit/${id}`}>
              Edit
            </Button>
            <Button className='ms-1' color='danger' outline tag={Link} to={`/compliance/list`}>
              Cancel
            </Button>
          </div>
        </CardBody>
      </Card>

    </Fragment>
  )
}

export default UserInfoCard
